.container {
  max-width: 600px;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
}

.loader {
  width: 32px;
  height: 32px;
  color: #fcdb03;
  border-radius: 50%;
  animation: fill 1s ease-in infinite alternate;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  height: 100%;
  width: 100%;
  border-radius: 50%;
  animation: fill .9s ease-in infinite alternate;
  position: absolute;
  top: 0;
  left: 50px;
}

.loader:after {
  animation-duration: 1.1s;
  left: auto;
  right: 48px;
}

@keyframes fill {
  0% {
    box-shadow: inset 0 0 0 2px;
  }

  100% {
    box-shadow: inset 0 0 0 10px;
  }
}

.breed-select {
  width: 50%;
  color: #00008b;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 10px;
}

.cat-info {
  flex-flow: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.cat-picture {
  max-width: 50%;
}

.error {
  color: red;
  margin-top: 20px;
  font-weight: bold;
  display: none;
}

.cat-descr {
  flex-direction: column;
  display: flex;
}

.name {
  font-size: 22px;
}

.description, .features {
  font-size: 16px;
}

